


























import {Vue, Component} from "vue-property-decorator";
import {trace_web} from "@/api";

@Component({
    components: {}
})
export default class TraceCodeInfo extends Vue {
    loading: boolean = false

    query: any = {
        pageNum: 1,
        pageSize: 10
    }

    list: any = {
        data: [],
        total: 0
    }

    columns: any[] = [
        {
            title: "追溯码编号",
            key: "traceCode",
            minWidth: 200
        },
        {
            title: "商品批次编号",
            key: "batchNumber",
            minWidth: 200
        },
        {
            title: "商品名称",
            key: "commodityName",
            minWidth: 150
        },
        {
            title: "客户名称",
            key: "customerName",
            minWidth: 150
        },
        {
            title: "合同名称",
            key: "contractName",
            minWidth: 150
        },
        {
            title: "单号",
            key: "orderNumber",
            minWidth: 200
        },
        {
            title: "出/入库",
            key: "type",
            minWidth: 100,
            render: (h: Vue.CreateElement, params: any) => {
                return h('span', {
                    domProps: {
                        innerHTML: params.row.type ? params.row.type.disName : '-'
                    }
                })
            }
        },
        {
            title: "生成时间",
            key: "createTime",
            minWidth: 220
        },
        {
            title: "操作",
            key: 'option',
            minWidth: 80,
            fixed: 'right',
            render: (h: Vue.CreateElement, params: any) => {
                let btn = [];
                if (this.$SSO_AUC('traceAct')) {
                    btn.push(
                        h('span', {
                            domProps: {
                                innerHTML: '追溯'
                            },
                            on: {
                                click: () => {
                                    this.goPage(params.row);
                                }
                            }
                        })
                    )
                }
                return h('div', {
                    class: 'handleBtn',
                }, btn)
            }
        }
    ]

    created() {
        this.queryList();
    }

    queryList(pageNum?: number) {
        this.query.pageNum = pageNum || 1;
        this.loading = true;
        trace_web.trace_web_traceCodeManager_queryTraceCodeList_post({
            data: {
                traceCode: this.$route.query.traceCode,
                batchNumber: this.$route.query.batchNumber,
                id: this.$route.query.id,
                ...this.query
            }
        }).then((res: any) => {
            this.list = res || {};
        }).finally(() => {
            this.loading = false;
        })
    }

    sizeChange(val: number) {
        this.query.pageSize = val;
    }

    goPage(row: any) {
        this.$router.push({
            path: '/commodityTrace',
            query: {
                traceCode: row.traceCode
            }
        });
    }

    goBack() {
        this.$emit('backEvent');
    }
}
